'use strict';

// MODULES //

import rad2degf from '@stdlib/math-base-special-rad2degf';
import asinf from '@stdlib/math-base-special-asinf';
import float64ToFloat32 from '@stdlib/number-float64-base-to-float32';


// MAIN //

/**
* Computes the arcsine (in degrees) of a single-precision floating-point number.
*
* @param {number} x - input value
* @returns {number} arcsine (in degrees)
*
* @example
* var v = asindf( 0.0 );
* // returns 0.0
*
* @example
* var v = asindf( 0.5  );
* // returns ~30.0
*
* @example
* import sqrtf from '@stdlib/math-base-special-sqrtf';
*
* var v = asindf( sqrtf( 2.0 ) / 2.0 );
* // returns ~45.0
*
* @example
* import sqrtf from '@stdlib/math-base-special-sqrtf';
*
* var v = asindf( sqrtf( 3.0 ) / 2.0 );
* // returns ~60.0
*
* @example
* var v = asindf( NaN );
* // returns NaN
*/
function asindf( x ) {
	return rad2degf( asinf( float64ToFloat32( x ) ) );
}


// EXPORTS //

export default asindf;
